import React, { useState } from 'react';

import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import ToggleButton from '@mui/material/ToggleButton';
import ToggleButtonGroup from '@mui/material/ToggleButtonGroup';

import SideItem from './SideItem';
import MainItem from './MainItem';
import content from '../utilities/content';
import Achievements from './Achievements';

function CV() {
    const [language, setLanguage] = useState('english');
    const { sideInfo, mainInfo, buttonText } = content[language];
    const { contactInfo, languages, software, strengths } = sideInfo;
    const { education, academicExp, extracurricular, achievements } = mainInfo;

    const handleChange = (e) => {
        setLanguage(e.target.value);
    }

    return (
        <Box sx={{ width: '90%', height: '100%' }}>

            <Card style={{ marginTop: '1rem', marginBottom: '1rem', paddingLeft: '1rem', paddingRight: '1rem', paddingBottom: '0.3rem' }}>
                <Grid container spacing={2} className="CV-body">
                    <Grid item xs={12} sm={8}>
                        <h1>Agustin D. Basila Ortiz</h1>
                    </Grid>
                    <Grid item xs={12} sm={4} style={{ display: 'flex', justifyContent: 'flex-end', alignItems: 'center' }}>
                        <ToggleButtonGroup
                            color="primary"
                            value={language}
                            exclusive
                            onChange={handleChange}
                        >
                            <ToggleButton value="english">{buttonText[0]}</ToggleButton>
                            <ToggleButton value="spanish">{buttonText[1]}</ToggleButton>
                        </ToggleButtonGroup>
                    </Grid>
                </Grid>
            </Card>

            <Grid container spacing={2} className="CV-body">
                <Grid item xs={12} lg={4}>
                    <Card>
                        <CardContent>
                            <SideItem {...contactInfo} />
                            <SideItem {...languages} />
                            <SideItem {...software} />
                            <SideItem {...strengths} />
                        </CardContent>
                    </Card>
                </Grid>
                <Grid item xs={12} lg={8}>
                    <Card>
                        <CardContent>
                            <MainItem {...education} />
                            <MainItem {...academicExp} />
                            <MainItem {...extracurricular} />
                            <Achievements {...achievements} />
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </Box>
    );
}

export default CV;