import React from 'react';
import './App.css';
import CV from './components/CV';


import Box from '@mui/material/Box';

function App() {
  const Container = {
    width: '100vw',
    height: '100vh',
    margin: '0',
    display: 'flex',
    justifyContent: 'center'
  };

  return (
    <Box sx={Container}>
      <CV />
    </Box>
  );
}

export default App;
