import React from 'react';
import './MainItem.css';

import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import { v4 as uuid } from 'uuid';

function MainItem(props) {
    const { title, items, tasks } = props;
    const [year, institution, position] = items;


    const generateList = () => {
        return (
            <>
                <p style={{ margin: '0' }}>{year}</p>
                <p style={{ margin: '0' }}>{institution}</p>
                <p style={{ marginBottom: '0' }}>{position}</p>
            </>
        )
    }

    const generateTasks = () => {
        return (
            tasks ? (
                <ul>
                    {tasks.map((text) => (<li key={uuid()}>{text}</li>))}
                </ul>
            ) : <></>
        );
    }
    return (
        <div className="MainItem">
            <Typography variant="h5" component="div">
                {title}
            </Typography>
            <Divider style={{ marginBottom: '.9rem' }} />
            {generateList()}
            {generateTasks()}
        </div>
    );
}

export default MainItem;